.about-product {
  &__item {
    @include grid();
    margin: 0 auto;

    &-title {
      grid-column: 1 / 3;

      @extend .section-title;

      &--color {
        @extend .section-title--color;
      }

      @include for-tablet() {
        display: none;
      }

      @include for-desktop() {
        display: none;
      }

      &--secondary {
        margin-bottom: 20px;

        @include for-tablet() {
          display: inline-block;
        }

        @include for-desktop() {
          display: inline-block;

          font-size: 36px;
          line-height: 44px;
          text-wrap: nowrap;
        }
      }
    }

    &-image {
      width: 100%;

      @include for-tablet() {
        width: 340px;
        height: 270px;
      }

      @include for-desktop() {
        width: 430px;
        height: 290px;
      }
    }

    &-wrapper {
      grid-column: 1 / 3;

      @include for-tablet() {
        grid-column: 4 / -1;
        height: 289px;
      }

      @include for-desktop() {
        grid-column: 9 / -1;
        height: 341px;
      }

      &--secondary {
        @include for-tablet() {
          grid-column: 1 / 5;

          margin-top: 107.5px;
          height: 213px;
        }

        @include for-desktop() {
          margin-top: 246px;
          height: 265px;
        }
      }

      &-title {
        display: none;
        margin-bottom: 20px;
        @extend .section-title;

        @include for-tablet() {
          display: inline-block;
          grid-column: 4 / 7;
          text-align: center;
        }

        @include for-desktop() {
          display: inline-block;
          grid-column: 7 / 13;
          text-align: center;

          margin-bottom: 30px;
          font-size: 36px;
          line-height: 44px;
        }
      }

      &-paragraph {
        @extend .section-paragraph;

        @include for-tablet() {
          grid-column: 4 / 7;
          height: 124px;
        }

        @include for-desktop() {
          grid-column: 9 / -1;
          height: 124px;
          width: 332px;
        }

        &--secondary {
          @include for-desktop() {
            width: 429px;
            height: 157px;
          }
        }
      }

      &-play-video-btn {
        display: block;
        margin: 32px auto 79px;
        border: none;
        background-image: url(../images/header-play-video-button.svg);
        background-repeat: no-repeat;
        background-color: transparent;
        background-size: contain;
        width: 240px;
        height: 66.05px;
        cursor: pointer;

        &:hover,
        &:focus {
          background-image: url(../images/header-play-video-button-hover.svg);
          transition: 0.3s;
        }

        @include for-tablet() {
          margin: 24px 0 65px;
          width: 247px;
        }
      }

      &-paragraph-customized {
        margin-bottom: 11px;
        font-size: 14px;
        font-weight: 400;
        line-height: 17px;
        letter-spacing: 0;
        color: $c-contrast;
      }

      &-img {
        @include for-tablet() {
          grid-column: 5 / -1;

          margin-top: 84px;
          display: block;
          height: 257px;
          width: 232px;
          background-image: url(../images/about-us-medium.png);
          background-size: cover;
          transform: translateX(-20px);
        }

        @include for-desktop() {
          grid-column: 7 / -1;

          margin-top: 183px;
          width: 495px;
          height: 345px;
          background-image: url(../images/about-us-large.png);
        }
      }
    }
  }
}
