.header {
  @include grid();

  margin: 0 auto;
  box-sizing: border-box;
  background-image: url(../images/header-small-background.svg);
  background-position: center 44px;
  background-repeat: no-repeat;
  background-size: 100% auto;

  height: 554px;

  @include for-tablet() {

    background-image: url(../images/header-medium-background.svg);
    background-position: right 6px;
    background-repeat: no-repeat;
    background-size: 486px 439px;
    height: 445px;
  }

  @include for-desktop() {
    background-image: url(../images/header-large-background-1.svg);
    background-position: right 26px;
    background-repeat: no-repeat;
    background-size: contain;
    height: 740px;
  }

  &__burger-menu {
    grid-column: 2 / 3;

    margin-left: auto;
    padding-left: 3.7px;
    display: block;
    cursor: pointer;
    background-color: transparent;
    border: none;

    @include for-tablet() {
      grid-column: 6 / 7;

      padding-left: 5.42px;
      height: 15px;
    }

    &-bar {
      background-color: $c-white;
      height: 0.5px;
      width: 16.3px;

      @include for-tablet() {
        width: 23.83px;
      }

      @include for-desktop {
        display: none;
      }

      &--margin {
        margin: 4.4px 0;
        width: 11.85px;
        transform: translateX(-3.7px);
        transition: transform 0.3s;

        @include for-tablet() {
          margin: 6.5px 0;
          width: 17.33px;
          transform: translateX(-5.42px);
        }
      }
    }

    &:hover .header__burger-menu-bar--margin {
      transform: translateX(0);
      background-color: $c-contrast;
      width: 16.3px;

      @include for-tablet() {
        width: 23.83px;
      }
    }

    &:hover .header__burger-menu-bar {
      background-color: $c-contrast;
    }
  }

  &__nav {
    display: flex;
    height: 44px;
    align-items: center;
    vertical-align: middle;

    grid-column: 1 / 3;

    @include for-tablet() {
      grid-column: 1 / 7;

      height: 74px;
    }

    @include for-desktop() {
      grid-column: 1 / 13;

      margin-top: 61px;
      height: 23px;
    }

    &-logo {
      width: 58px;
      height: 12px;

      @include for-tablet() {
        width: 67px;
        height: 14px;
      }

      @include for-desktop() {
        width: 77.5px;
        height: 16px;
      }

      &-icon {
        width: 58px;
        height: 12px;
        object-fit: cover;

        @include for-tablet() {
          width: 67px;
          height: 14px;
        }

        @include for-desktop() {
          width: 77.5px;
          height: 16px;
        }
      }
    }

    &-menu {
      display: none;
      width: 320px;

      @include for-desktop() {
        display: flex;
        margin-left: 27px;
        width: 711px;
        height: 23px;
        grid-column: 2 / 8;
        align-items: center;
      }

      &-item {
        justify-content: center;

        &:not(:first-child) {
          margin-left: 36px;
        }

        &--language {
          margin-right: auto;
        }
      }

      &-language {
        display: none;
        position: absolute;
        bottom: -10px;
        background-color: transparent;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        letter-spacing: 0;
        text-align: left;
        color: $c-contrast;
        border: none;
        cursor: pointer;
      }

      &-link {
        text-decoration: none;
        color: $c-white;
        font-size: 16px;
        font-weight: 500;
        line-height: 21px;
        letter-spacing: 0;

        &:hover {
          color: $c-contrast;
          transition: 0.3s;
        }
      }
    }
  }

  &__buy-button {
    display: none;
    @extend .btn-primary-basis;

    @include for-desktop() {
      margin-left: 37px;
      display: flex;
    }
  }

  &__content {
    grid-column: 1 / 3;

    @include for-tablet() {
      grid-column: 1 / 4;
    }

    @include for-desktop() {
      margin-top: 96px;
      grid-column: 1 / 13;
    }

    &-title {
      margin-top: 152px;

      font-size: 26px;
      font-weight: 900;
      line-height: 34px;
      letter-spacing: 0;
      color: $c-white;
      text-wrap: nowrap;

      &--color {
        color: $c-contrast;
        font-weight: 400;
        text-wrap: nowrap;
      }

      @include for-tablet() {
        margin-top: 5px;
      }

      @include for-desktop() {
        margin-top: 5px;

        font-size: 46px;
        font-weight: 900;
        line-height: 60px;
        letter-spacing: 0;
      }
    }

    &-paragraph {

      margin-top: 17px;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: 0;
      color: #929292;

      @include for-desktop() {
        width: 336px;
        margin: 19px 0 0 97px;
      }
    }

    &-price {
      margin-top: 8px;
      text-align: center;
      font-size: 18px;
      font-weight: 500;
      line-height: 22px;
      letter-spacing: 0;
      color: #fff;
      align-items: center;

      @include for-tablet() {
        margin-top: 10px;
        text-align: left;
      }

      @include for-desktop() {
        width: 336px;
        margin: 20px 0 0 97px;
      }
    }

    &-play-video-btn {
      display: block;
      margin: 16px auto 0;
      border: none;
      background-image: url(../images/header-play-video-button.svg);
      background-repeat: no-repeat;
      background-color: transparent;
      background-size: contain;
      width: 240px;
      height: 66.05px;
      cursor: pointer;

      &:hover {
        background-image: url(../images/header-play-video-button-hover.svg);
        transition: 0.3s;
      }

      @include for-tablet() {
        margin: 24px 0 65px;
        width: 247px;
      }

      @include for-desktop() {
        margin: 19px 0 0 93px;
      }
    }

    &-buy-now-btn {
      position: fixed;
      bottom: 0;
      left: 20px;

      margin-top: 23px;
      border: none;
      width: calc(100% - 40px);
      height: 40px;
      background: $c-contrast;
      color: $c-white;
      text-decoration: none;
      border-radius: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 4;

      @include for-tablet() {
        display: none;
      }

      @include for-desktop() {
        display: none;
      }
    }
  }

  &__utilis {
    display: none;

    @include for-desktop() {
      grid: 1 / 13;

      margin: 64px 0;
      display: flex;
      width: 1055px;
      height: 37px;
      color: $c-white;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: 0;
    }

    &-faq {
      @extend .utilis-btn;

      &-link {
        text-decoration: none;
        color: $c-white;

        &:hover {
          color: $c-contrast;
          transition: 0.3s;
        }
      }
    }

    &-help {
      @extend .utilis-btn;
      margin-left: 40px;

      &-link {
        text-decoration: none;
        color: $c-white;

        &:hover {
          color: $c-contrast;
          transition: 0.3s;
        }
      }
    }

    &-more {
      position: relative;
      margin-left: 404px;
      text-align: center;
      color: $c-contrast;
      cursor: pointer;
      text-decoration: none;

      &-arrow {
        position: absolute;
        top: 30px;
        left: 14.5px;
        font-size: 22px;
      }
    }

    &-image {
      margin-left: 298px;
      width: 205px;

      &-previous-btn {
        @extend .utilis-btn;
        color: $c-text;
        width: 66px;
        cursor: default;
      }

      &-next-btn {
        @extend .utilis-btn;
        margin-left: 98px;
        color: $c-text;
        width: 36px;
        cursor: default;
      }

      &-previews {
        position: relative;

        &-range {
          margin-top: 10px;
          content: "";
          width: 207px;
          height: 1px;
          background-color: #212121;
        }

        &-target {
          position: absolute;
          top: 0;
          width: 69px;
          height: 1px;
          background-color: $c-contrast;
        }
      }
    }
  }
}
